import Vue from 'vue'
import App from './App.vue'
import Vuelidate from 'vuelidate'
import router from './router'
import store from './store'
import dateFilter from '@/filters/date.filter'
import messagePlugin from '@/utils/message.plugin'
import Loader from '@/components/app/Loader'
import tooltipDirective from '@/directives/tooltip.directive'
import Paginate from 'vuejs-paginate'
import 'materialize-css/dist/js/materialize.min'

import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'
import currencyFilter from './filters/currency.filter'
import localizeFilter from './filters/localize.filter'

Vue.config.productionTip = false

Vue.use(messagePlugin)
Vue.filter('date', dateFilter)
Vue.filter('currency', currencyFilter)
Vue.filter('localize', localizeFilter)
Vue.directive('tooltip', tooltipDirective)
Vue.use(Vuelidate)
Vue.component('Loader', Loader)
Vue.component('Paginate', Paginate)



firebase.initializeApp({
  apiKey: "AIzaSyBOmXIjrO1cstgJERk5RD4cfLhW99gNagc",
  authDomain: "vue-crm-minin-1f30b.firebaseapp.com",
  databaseURL: "https://vue-crm-minin-1f30b.firebaseio.com",
  projectId: "vue-crm-minin-1f30b",
  storageBucket: "vue-crm-minin-1f30b.appspot.com",
  messagingSenderId: "753791342503",
  appId: "1:753791342503:web:e7a17bd9e3f1c33aa4c882"
})

let app

firebase.auth().onAuthStateChanged(()=>{
  if (!app){
    app = new Vue({
    router,
    store,
    render: h => h(App)
    }).$mount('#app')
  }
})


